section.schedule .container {
  max-width: 800px;
}

section.schedule ul.day {
  padding-left: 50px;
  margin-bottom: 20px;
}

section.schedule ul.day .day-title {
  box-sizing: border-box;
  color: #64d861;
  display: inline-block;
  margin-left: -50px;
  width: 50%;
  max-width: 220px;
  margin-bottom: 10px;
}

section.schedule ul.day .day-title h3 {
  font-weight: 500;
  margin: 5px 25px;
  font-size: 18px;
}

@media only screen and (max-width: 640px) {
  section.schedule ul.day .day-title {
    max-width: 90%;
    width: 90%;
  }
}

section.schedule ul.day li.item {
  margin-left: -10px;
  margin-right: -10px;
  margin-bottom: 0;
  list-style: none;
  width: 20%;
  min-width: 400px;
  font-size: 0;
}

section.schedule ul.day li.item:before,
section.schedule ul.day li.item:after {
  content: "";
  display: table;
}

section.schedule ul.day li.item:after {
  clear: both;
}

@media only screen and (max-width: 640px) {
  section.schedule ul.day li.item {
    font-size: 1.2em;
  }
}

section.schedule ul.day li.item h6.time {
  box-sizing: border-box;
  float: left;
  margin-bottom: 0;
  width: 25%;
  padding-left: 10px;
  padding-right: 10px;
  display: inline-block;
  color: #64d861;
  font: 16px "Avenir Next", sans-serif;
  display: inline-block;
  vertical-align: 0.25;
}

@media only screen and (max-width: 640px) {
  section.schedule ul.day li.item h6.time {
    box-sizing: border-box;
    float: left;
    margin-bottom: 0;
    width: 33.33333%;
    padding-left: 10px;
    padding-right: 10px;
  }
}

section.schedule ul.day li.item .description {
  box-sizing: border-box;
  float: left;
  margin-bottom: 0;
  width: 75%;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 16px;
  display: inline-block;
  vertical-align: 0.75;
  padding-left: 0;
  display: inline-block;
}

@media only screen and (max-width: 640px) {
  section.schedule ul.day li.item .description {
    box-sizing: border-box;
    float: left;
    margin-bottom: 0;
    width: 66.66667%;
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media only screen and (max-width: 640px) {
  section.schedule ul.day li.item {
    width: 100%;
    min-width: 0;
  }
}
